body {
	font-family:  'Open Sans', sans; }
.container {
	max-width: 1180px;
	margin: 0 auto;
	@include r(1200) {
		margin: 0 20px; } }

.hero {
	min-height: 100vh;
	width: 100%;
	@include r(750) {
		height: 100%;
		padding-bottom: 50px; } }
.header {
	padding-top: 5px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline;
	@include r(550) {
		flex-direction: column; } }


.logo {
	@include r(550) {
		margin: 0 auto;
		margin-top: 20px; }

	img {
		height: 32px;
		@include r(550) {
			margin-bottom: 20px; } } }


nav {
	@include r(550) {
		margin: 0 auto; }

	ul {
		position: relative;
		top: -3px;
		li {
			display: inline-block;
			@include r(550) {
				display: block;
				// &:first-of-type
				// 	margin-left: -30px
				text-align: center;

				padding-bottom: 15px; }

			a {
				font-size: 1rem;
				color: #d1d2d2;
				text-decoration: none;
				// margin-left: 30px
				padding: 15px;
				position: relative;
				&:before {
					position: absolute;
					left: 15px;
					top: 15px;
					content: attr(data-content);
					display: inline-block;
					color: #fff;
					width: 100%;
					clip: rect(0,0,0,0);
					transition: clip cubic-bezier(0.25, 0.46, 0.45, 0.94) 900ms;
					@include r(550) {
						left: 0;
						padding-left: 0; } }

				&:hover {
					&:before {
						clip: rect(0,100px,100px,0); } } } } } }


.main {
	margin-top: 120px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	@include r(901) {
		margin-top: 50px; }


	&__title {
		text-align: center;
		color: #FFF;
		font-size: 53px;
		font-weight: 300;
		line-height: 1.2;
		margin-bottom: 42px; }
	&__subtitle {
		font-size: 18px;
		text-align: center;
		color: #FFF;
		font-weight: 300;
		margin-bottom: 25px; } }
.btn {
	position: relative;
	// left: 50%
	// transform: translate(-50%, 0)

	display: inline-block;
	color: #FFF;
	margin: 0 auto;
	margin-bottom: 30px;
	margin-top: 70px;
	font-size: 16px;
	text-decoration: none;
	line-height: 55px;
	padding: 0 30px;
	border-radius: 30px;

 }	// background: rgb(52, 196, 130)

